export const getAll = async (value_search) => {
    let valor;
    if (value_search.toLowerCase() === "any quiroz") {

        valor = "sabes reik";
    }
    else {
        valor = value_search;
    }
    const url = `https://deezerdevs-deezer.p.rapidapi.com/search?q=${encodeURI(valor)}`;

    let resp = await fetch(url, {
        "method": "GET",
        "headers": {
            "x-rapidapi-key": "5cb7706abcmsh52ed7934a99a06ep15d9a2jsnffa41311ac30",
            "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com"
        }
    });


    const { data } = await resp.json();

    const arrayObject = data.map(el => {
        return (
            {
                id: el.id,
                title: el.title_short,
                artist: el.artist.name,
                img: el.album.cover_medium,
                img_xs: el.album.cover_small,
                mp3: el.preview,
                album: el.album.title
            }

        );

    });

    return arrayObject;
}