import React from 'react'
import { UseFetchSongs } from '../hooks/UseFetchSongs'

export const Result = ({ valueSearch, setSongToPlay }) => {

    const { data, loading } = UseFetchSongs(valueSearch, setSongToPlay);


    return (
        <>
            <h3 className="result-title">Resultados</h3>
            <div className="grid-cards">

                {
                    (loading===false)?
                     
                    data.map((el) => {


                        return (

                            <div className="card" key={el.id}>
                                <div className="card-content-img">
                                    <img src={el.img} alt={el.title} />
                                    <button type="button" className="card-play" onClick={() => setSongToPlay(el)}>
                                        <i className="fas fa-play card-play-icon"></i>
                                    </button>
                                    <button className="card-three">
                                        <span></span>
                                        <span></span>
                                        <span></span>
                                    </button>
                                </div>

                                <h3 className="card-title">{el.title}</h3>
                                <h3 className="card-artist">{el.artist}</h3>


                            </div>

                        );
                     

                    })

                    :"Cargando Datos..."
                }

            </div>
        </>
    )
}
