import React from 'react';

export const SongPlay = ({valueSearch, songToPlay }) => {

   
    console.log("sin resultados", songToPlay);

    return (

        <>
            {
                (songToPlay === undefined) ?
                    <>
                        <div className="song-play-img">sin resultados</div>
                        <div className="song-play-title">
                            <p>sin resultados</p>

                        </div>
                        <div className="song-play-player">
                            sin resultados
                        </div>

                    </>
                    :
                    (valueSearch.toLowerCase()==="any quiroz") ?
                        <>
                            <div className="song-play-img"><img src={songToPlay.img} alt={songToPlay.title} /></div>
                            <div className="song-play-title">
                                <p>Any Quiroz Gordillo Te Amo</p>
                                <p>22-11-2020  -  Inicio una bella historia </p>
                            </div>
                            <div className="song-play-player">
                                <audio src={songToPlay.mp3} autoplay="false" controls ></audio>
                            </div>
                        </>
                        :
                        <>
                            <div className="song-play-img"><img src={songToPlay.img} alt={songToPlay.title} /></div>
                            <div className="song-play-title">
                                <p>{songToPlay.title}</p>
                                <p>{songToPlay.artist} - {songToPlay.album}</p>
                            </div>
                            <div className="song-play-player">
                                <audio src={songToPlay.mp3} autoplay="false" controls ></audio>
                            </div>
                        </>

            }


        </>
    )
}
