
import { useEffect, useState } from 'react'
import { getAll } from '../helpers/getAll';


export const UseFetchSongs = (value_search,setSongToPlay) => {

    const [state, setstate] = useState({
        data: [],
        loading: true,
    });

    


    useEffect(() => {

        getAll(value_search)
            .then(elements => {
                setstate({
                    data: elements,
                    loading: false,
                });
                setSongToPlay(elements[0]);

            });


    }, [value_search]);

    return state; //{data:[], loading:true}
}
